import { NextRequest, NextResponse } from 'next/server';

export async function GET(req: NextRequest) {
  const { searchParams } = new URL(req.url);
  const url = searchParams.get('url');

  if (!url) {
    return NextResponse.json({ error: 'URL parameter is required' }, { status: 400 });
  }

  try {
    const decodedUrl = decodeURIComponent(url);
    
    // Basic validation to prevent fetching internal resources
    const urlObject = new URL(decodedUrl);
    if (['localhost', '127.0.0.1'].includes(urlObject.hostname)) {
        return NextResponse.json({ error: 'Fetching local resources is not allowed' }, { status: 403 });
    }

    const response = await fetch(decodedUrl, {
      headers: {
        'Accept': 'application/json, image/*',
      },
    });

    if (!response.ok) {
      return NextResponse.json(
        { error: `Failed to fetch: ${response.statusText}` },
        { status: response.status }
      );
    }

    // Stream the response back to the client
    return new NextResponse(response.body, {
      status: response.status,
      statusText: response.statusText,
      headers: response.headers,
    });
    
  } catch (error) {
    console.error('Proxy error:', error);
    return NextResponse.json(
        { error: 'An error occurred while proxying the request.' }, 
        { status: 500 }
    );
  }
} 